* {
  box-sizing: border-box;
  outline: none;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  color: #858fad;
  text-align: center;
  background: url("bg-pattern.5651018f.svg") 0 -50vh / cover no-repeat;
  font-family: Manrope, sans-serif;
  font-size: 1.5rem;
  font-weight: 600;
  line-height: 1;
}

.container {
  min-height: 100vh;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 3rem;
  display: flex;
}

header {
  margin-bottom: 5rem;
  padding-top: 8rem;
  position: relative;
}

header h1 {
  color: #293356;
  z-index: 100;
  margin-bottom: 2rem;
  position: relative;
}

header div {
  height: 145px;
  width: 146px;
  z-index: -200;
  background-image: url("pattern-circles.6217120f.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: 3.8rem;
  left: 50%;
  transform: translateX(-50%);
}

.cart {
  width: 50rem;
  padding-top: 5rem;
  background-color: #fff;
  border-radius: 12px;
  padding-inline: 4rem;
  box-shadow: 0 0 15px #cfd3e2;
}

.cart .cart-price {
  flex-direction: "row";
  justify-content: space-between;
  align-items: center;
  margin-bottom: 4rem;
  padding-inline: 1rem;
  display: flex;
}

.cart .cart-price .pages-views {
  color: #10d5c2;
  text-transform: uppercase;
  letter-spacing: 3px;
}

.cart .cart-price .price-container {
  flex-direction: "row";
  justify-content: center;
  align-items: center;
  display: flex;
}

.cart .cart-price .price {
  color: #293356;
  font-size: 2.8rem;
  font-weight: 800;
}

.cart .progress {
  flex-direction: "row";
  justify-content: flex-end;
  align-items: center;
  gap: 1rem;
  font-size: 1.2rem;
  display: flex;
}

.cart .progress .discount {
  color: #ff8c66;
  background-color: #feece7;
  border-radius: 12px;
  padding: .75rem;
  display: inline-block;
}

.cart .text {
  flex-direction: "row";
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3rem;
  font-size: 1.2rem;
  display: flex;
}

.cart .text ul {
  text-align: left;
  line-height: 2.5rem;
  list-style: url("icon-check.e1420a70.svg");
}

.cart .text .btn {
  color: #fff;
  cursor: pointer;
  background-color: #293356;
  border: none;
  border-radius: 25px;
  padding: 1rem 4rem;
}

.slider {
  width: 100%;
  margin-bottom: 3rem;
}

.slider .my-range {
  width: 100%;
  appearance: none;
  height: 10px;
  background: linear-gradient(to right, #10d5c2 0% 50%, #eaeefb 50% 100%);
  border-radius: 5px;
  outline: none;
}

.slider .my-range::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 4rem;
  height: 4rem;
  cursor: pointer;
  background-color: #10d5c2;
  background-image: url("icon-slider.b7adef45.svg");
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 50%;
}

.slider .my-range::-webkit-slider-thumb:hover {
  box-shadow: 0 0 10px #10d5c2;
}

.slider .my-range::-webkit-slider-runnable-track {
  -webkit-appearance: none;
  box-shadow: none;
  background: none;
  border: none;
}

.switch {
  width: 40px;
  height: 20px;
  cursor: pointer;
  background-color: #a5f3eb;
  border-radius: 20px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
}

.slider-checkbox {
  height: 16px;
  width: 16px;
  background-color: #fff;
  border-radius: 50%;
  transition: all .5s;
  position: absolute;
  top: 2px;
  left: 2px;
}

.switch input:checked + .slider-checkbox {
  transform: translateX(20px);
}

hr {
  opacity: .1;
  border: 1px solid #000;
  margin-block: 3rem;
  margin-inline: -4rem;
}

